$colorRed: #AA0601;
$colorRed-text:#650300;
$colorWhite:#fff;
$colorPink-bg:#FBF2F2;
$colorGrey:#707070;
/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}

body {
  line-height: 1;
}

ol,
ul {
  list-style: none;
}

blockquote,
q {
  quotes: none;
}

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: '';
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}





.white {
  color: #fff;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

a {
  text-decoration: none;
}

h1 {
  font-size: 64px;
  line-height: 96px;
}

h2 {
  font-size: 48px;
  line-height: 72px;
}

h3 {
  font-size: 36px;
  line-height: 54px;
}

h4 {
  font-size: 24px;
  line-height: 36px;
}

p {
  font-size: 16px;
  line-height: 24px;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

.container {
  max-width: 1320px;
  margin: 0 auto;
  padding: 0 12px;
}

//footer 置底

html,
body {
  height: 100%;
  margin: 0;
}

body {
  display: flex;
  flex-direction: column;
  font-family: 'Noto Sans TC', sans-serif;
}

.wrapper {
  flex-grow: 1;
}

//header

.header {
  background-color: $colorRed;
}

.header img {
  width: 80px;
  padding-top: 16px;
  padding-bottom: 16px;
}

.header .container,
.header-item ul {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header-item a {
  margin-left: 64px;
  padding-top: 25px;
  padding-bottom: 25px;
  color: #fff;
}

.header .container .fa-bars {
  display: none;
}

.header-body {
  display: none;
}

@media(max-width:767px) {
  .headerRwd {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
  }

  .headerRwd .item a {
    padding: 12px 60px;
    line-height: 24px;
    color: $colorWhite;
    display: block;
    border: 1px solid #fff;
  }

  .headerRwd .item {
    width: 50%;
    text-align: center;
  }
}


//banner
.banner {
  background-image: url(https://raw.githubusercontent.com/hexschool/2022-web-layout-training/main/week3-4/home-header-sm.png);
  height: 787px;
}

.banner,
.coBranding {
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}

.colorRed-text {
  color: $colorRed-text;
}

.banner-text {
  text-align: right;
}

.banner-body {
  align-items: center;
  display: flex;
  height: 787px;
  justify-content: flex-end;
}

.banner-text button {
  font-size: 20px;
  line-height: 30px;
  color: $colorWhite;
  padding: 6px 12px;
  border: none;
}

.bg-red {
  background-color: $colorRed;
}

@media(max-width:767px) {
  .banner-text h3 {
    font-size: 16px;
    line-height: 24px;
  }

  .banner-text h1 {
    font-size: 24px;
    line-height: 36px;
  }

  .banner,
  .banner-body {
    height: 396px;
  }
}


//service

.service {
  background-color: $colorPink-bg;
}

.service h2 {
  text-align: center;
  padding-top: 80px;
  padding-bottom: 32px;
  color: $colorRed-text;
}

.service-body h4 {
  margin-bottom: 8px;
  text-align: center;
  color: $colorRed-text;
}

.service-body {
  display: flex;
  flex-wrap: wrap;
  padding-bottom: 80px;
}

.service-body .card {
  width: 25%;
  padding-right: 24px;
}

.service-body .card:nth-child(4) {
  padding-right: 0;
}

.service-body .card img {
  width: 83.83px;
  margin: 0 auto;
  margin-bottom: 24px;
}

@media(max-width:992px) {
  .service-body .card {
    width: 50%;
  }
}

@media(max-width:767px) {
  .service-body .card {
    width: 100%;
  }

  .service h2 {
    font-size: 24px;
    line-height: 36px;
  }
}

//classic

.classic h2,
.coBranding h2,
.customer h2,
.contact h2 {
  text-align: center;
  padding-top: 80px;
  padding-bottom: 48px;
  color: $colorRed;

}

.classic-body h2,
.coBranding-body h2,
.customer-body h2 {
  font-family: 'Roboto', sans-serif;
  padding-top: 8px;
  font-style: italic;
  font-weight: 600;
}

.classic-body,
.coBranding-body,
.customer-body {
  display: flex;
  flex-wrap: wrap;
}

.classic-body .card {
  width: 33.333%;
  padding-right: 24px;
}

@media(max-width:992px) {
  .classic-body .card {
    width: 100%;
  }

  .classic .container {
    max-width: 336px;
    margin: 0 auto;
  }
}

@media(max-width:767px) {
  .classic .container {
    max-width: 1320px;
  }

  .classic-body .card {
    padding-right: 0;
  }
}


//coBranding

.coBranding-body .card {
  width: 50%;
}

.coBranding .container h2 {
  color: $colorWhite;
}

.coBranding {
  background-image: url(https://raw.githubusercontent.com/hexschool/2022-web-layout-training/main/week3-4/index-bg-md.png);
}

.coBranding-body {
  padding-bottom: 80px;
}

.coBranding-body .card h2 {
  background-color: $colorWhite;
  color: $colorRed;
  padding-top: 8px;
  padding-bottom: 8px;
}

.coBranding-image-sm {
  display: none;
}

@media(max-width:992px) {
  .coBranding-body .card {
    width: 100%;
    margin-right: 14px;
    margin-left: 14px;
  }

  .coBranding-body .card:nth-child(1) {
    margin-bottom: 24px;
  }
}

@media(max-width:767px) {
  .coBranding-image-sm {
    display: block;
  }

  .coBranding-image-md {
    display: none;
  }
}

//customr

.customer {
  padding-bottom: 80px;
}

.customer-body .card {
  width: calc(25% - 24px);
  border: 1px solid #DEE2E6;
  box-shadow: 0px 2px 6px #00000029;
  margin-right: 24px;
}

.customer-body .card:nth-child(4) {
  margin-right: 0;
}

.customer-text {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.column {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
}

.customer-name {
  padding: 16px;
  padding-bottom: 0;
}

.customer-comment {
  padding: 16px;
  padding-bottom: 72px;
}

.column p {
  padding: 16px;
  color: $colorGrey;
}


@media(max-width:992px) {
  .customer-body .card {
    width: calc(50% - 36px);
  }

  .customer-body .card {
    margin-left: 24px;
    margin-bottom: 24px;
  }

  .customer-body .card:nth-child(2),
  .customer-body .card:nth-child(4) {
    margin-left: 12px;
  }

  .customer-body .card:nth-child(1),
  .customer-body .card:nth-child(3) {
    margin-right: 12px;


  }
}

@media(max-width:767px) {
  .customer-body .card {
    width: 100%;

  }

  .customer-body .card:nth-child(2),
  .customer-body .card:nth-child(4) {
    margin: 0;
    margin-bottom: 16px;
  }

  .customer-body .card:nth-child(1),
  .customer-body .card:nth-child(3) {
    margin: 0;
    margin-bottom: 16px;


  }
}


//contact

.contact {
  background: $colorPink-bg;
}

.contact-body {
  width: 50%;
  margin: 0 auto;
}

.contact-body .card span {
  font-size: 16px;
  line-height: 30px;
  margin-bottom: 24px;
}

.contact-input input,
.contact-input textarea {
  width: 100%;
}

.contact-body .card input,
.contact-body .card textarea {
  margin-bottom: 16px;
}

.contact .card input,
.contact .card textarea {
  margin-top: 8px;
  font-size: 16px;
  line-height: 24px;
  background-color: transparent;
  padding: 6px 12px;
}

.contact .contact-text {
  margin-bottom: 14px;
  font-size: 20px;
  line-height: 30px;
}

.contact-button {
  display: flex;
  justify-content: center;
}

.contact-button button {
  background: $colorRed;
  color: #fff;
  padding: 16px 176px;
  border: none;
  font-size: 16px;
  line-height: 24px;
  margin-top: 64px;
  margin-bottom: 80px;
}

.contact-body .contact-privacy input {
  background-color: transparent;
}

@media(max-width:992px) {
  .contact-button button {
    padding: 16px 136px;
  }

  .contact-body {
    width: 75%;
  }
}

@media(max-width:767px) {
  .contact-button button {
    padding: 16px 142px;
  }

  .contact-body {
    width: 100%;
  }
}




//footer

.footer {
  background-color: $colorRed;
}

.footer-item,
.privacy,
.privacy-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.footer-item {
  padding-top: 60px;
}

.footer-nav,
.footer-logo {
  display: flex;
}

.footer-nav a {
  color: #fff;
  font-size: 16px;
  line-height: 24px;
  padding-right: 48px;
}

.footer-logo img {
  height: 48px;
  padding-left: 32px;
}

.phone {
  padding-top: 35px;
  padding-top: 8px;
}

.email {
  padding-bottom: 32px;
}

.border {
  border: 1px solid #fff;
}

.privacy {
  padding-top: 32px;
  padding-bottom: 32px;
}

.privacy-item a {
  margin-left: 48px;
  color: #fff;
  font-size: 14px;
  line-height: 21px;
}

@media(max-width:992px) {
  .footer-logo img {
    padding-left: 16px;
    height: 40px;
  }



}

@media(max-width:767px) {
  .footer-nav {
    display: none;
  }

  .container {
    max-width: 720px;
    margin: 0 auto;
  }

  .phone,
  .email {
    font-size: 16px;
    line-height: 24px;
  }

  .email {
    padding-bottom: 24px;
  }

  .footer-logo img {
    height: 24px;
  }

  .footer-logo {
    position: relative;
    right: 0;
    top: 26px;
    padding-right: 14px;
    width: 100%;
    justify-content: flex-end;
  }

  .footer-item {
    padding-top: 0;
  }

  .privacy-item {
    flex-wrap: wrap;
  }

  .phone {
    padding-top: 0;
  }

  .privacy {
    flex-wrap: wrap;
    padding-top: 16px;
  }

  .privacy-item a {
    margin-left: 0;
    font-size: 16px;
    line-height: 24px;
  }

  .privacy-item {
    display: block;
  }

  .header-item {
    display: none;
  }

  .header .container .fa-bars {
    display: block;
    font-size: 30px;
  }
}